'use strict';
let game = document.querySelector('#game');
let field = game.querySelector('.field');

const rowsNum = 20;
const colsNum = 30;
const gamers = ['gamer1','gamer2']; //–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∏–≥—Ä–æ–∫–æ–≤
let gamerNum = 0; //–∫—Ç–æ –Ω–∞—á–∏–Ω–∞–µ—Ç —Ö–æ–¥–∏—Ç—å –ø–µ—Ä–≤—ã–π

// –∑–∞–ø–æ–ª–Ω—è–µ—Ç –∏–≥—Ä–æ–≤–æ–µ –ø–æ–ª–µ —è—á–µ–π–∫–∞–º–∏ + –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –∫–æ–ª-–≤–æ —è—á–µ–µ–∫
const rows = fieldCreator(field, rowsNum, colsNum);
const columns = getColumns(rows);
const diagonale = getDiagonale(rows)

let lines = rows.concat(columns, diagonale);
console.log(lines);
function checkWin(gamer, lines) {
    for (let i = 0; i < lines.length; i++) {
        for (let j = 4; j < lines[i].length; j++) {
            if (
            lines[i][j - 4].classList.contains(gamer) &&
            lines[i][j - 3].classList.contains(gamer) &&
            lines[i][j - 2].classList.contains(gamer) &&
            lines[i][j - 1].classList.contains(gamer) && 
            lines[i][j].classList.contains(gamer)) {
                return true;
            }
        }
    }
    return false;
}

function isWin(gamers, lines) {
    for (let i = 0; i < gamers.length; i++) {
        if (checkWin(gamers[i], lines)) {
            endGame(gamers[i]);
            break;
        }
    }
}

function endGame(gamer) {
    alert(`${gamer} is win!`);
    freezeField(field);
}

function freezeField(field) {
    let cells = field.querySelectorAll('td');
    for (let i = 0; i < cells.length; i++) {
        cells[i].removeEventListener('click', cellClickHandler);
    }
}

// –ø–æ–¥—Å–≤–µ—Ç–∫–∞ —è—á–µ–µ–∫ –ø—Ä–∏ –∫–ª–∏–∫–µ
function cellClickHandler(e) {
    this.classList.add(gamers[gamerNum]);
    this.removeEventListener('click', cellClickHandler);
    isWin(gamers, lines)
    //–µ—Å–ª–∏ –Ω–æ–º–µ—Ä –∏–≥—Ä–æ–∫–∞ > –¥–ª–∏–Ω—ã –º–∞—Å—Å–∏–≤–∞ -> —Å–±—Ä–∞—Å—ã–≤–∞–µ–º –Ω–æ–º–µ—Ä –¥–æ 0
    gamerNum = gamerNum >= gamers.length - 1 
    ? 0 
    : gamerNum + 1;
}

function fieldCreator(field, rowsNum, colsNum) {
    let rows = [];
    for (let i = 0; i < rowsNum; i++) {
        let tr = document.createElement('tr');
        rows[i] = [];

        for (let j = 0; j < colsNum; j++) {
            let td = document.createElement('td');
            tr.append(td);
            td.addEventListener('click', cellClickHandler);
            rows[i][j] = td; 
        }

        field.append(tr);
    }
    return rows;
}   

function getColumns(rows) {
    let columns = [];
    for (let i = 0; i < rows.length; i++) {
        if (columns[i] === undefined) columns[i] = []; //—Å–æ–∑–¥–∞–Ω–∏–µ –ø–æ–¥–º–∞—Å—Å–∏–≤ [ [] ]
        for (let j = 0; j < rows.length; j++) {
            
            columns[i][j] = rows[j][i]; //–∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ –ø–æ–¥–º–∞—Å—Å–∏–≤–∞ [ [1,4,6...] ]
        }
    }
    return columns;
}

function getDiagonale(rows) {
    let result = [];
    for (let i = 0; i < rows.length; i++) {
        for (let j = 0; j < rows[i].length; j++) {
           if (result[i+j] === undefined) result[i+j] = [];
           result[i+j].push(rows[i][j]);
        }
    }
    return result;
}